.social-links {
  max-width: 284px;
  width: 100%; }

.social-links__item {
  position: relative; }
  .social-links_round > .social-links__item:first-child {
    top: -29px; }
  .social-links_round > .social-links__item:nth-child(2) {
    margin-right: 3px; }
  .social-links_round > .social-links__item:nth-child(3) {
    margin-left: 3px; }
  .social-links_round > .social-links__item:last-child {
    top: -29px; }



